            <b-row v-if="method_credit_card && completition == 1 && product.type == 'TRANSACTION'" class="mt-2">
            <small>* Pagamento parcelado com acréscimo de 2 a.m.</small>
            </b-row>

          </div>

       

      </b-col>


    </b-row>

  </b-container>
</template>
<script>
import PaymentService from "@/services/resources/PaymentService";
import ProductService from "@/services/resources/ProductService";
import AffiliateService from "@/services/resources/AffiliateService";
import UserSettingsService from "@/services/resources/UserSettingsService";
import GlobalSettingsService from "@/services/resources/GlobalSettingsService";
import ClientTempService from "@/services/resources/ClientTempService";
import ClientStatisticsService from "@/services/resources/ClientStatisticsService";
import axios from 'axios';
import Cookies from 'js-cookie';
import pagarme from '@/assets/js/pagarme.min';
import UAParser from 'ua-parser-js';

const servicePayment = PaymentService.build();
const serviceProduct = ProductService.build();
const serviceAffiliate = AffiliateService.build();
const serviceSettings = UserSettingsService.build();
const serviceGlobalSettings = GlobalSettingsService.build();
const serviceClient = ClientTempService.build();
const serviceClientStatistics = ClientStatisticsService.build();

export default {
  name: 'Login',
  data () {
    return {
      data: {
        name: '',
        email: '',
        cellphone: '',
        cpf_cnpj: '',
        card_number: '',
        card_holder_name: '',
        card_valid: '',
        card_expiration_date: '',
        card_year: '',
        card_month: '',
        card_cvv: '',
        zipcode: '',
        street: '',
        number: '',
        complement: '',
        neighborhood: '',
        city: '',
        state: ''
      },
      line_width: 240,
      step: 300,
      completition: 0,
      ticket_url: '',
      installments: 12,
      submit: false,
      charge_success: false,
      charge_error: false,
      card_number_error: false,
      loading: false,
      firstStep: false,
      secoundStep: true,
      currentCardBackground: Math.floor(Math.random()* 25 + 1), 
      minCardYear: new Date().getFullYear(),
      amexCardMask: "#### ###### #####",
      otherCardMask: "#### #### #### ####",
      cardNumberTemp: "",
      isCardFlipped: false,
      focusElementStyle: null,
      isInputFocused: false,
      method_credit_card: true,
      product: {
        images: [{ path: ''}],
        name: '',
        description: ''
      },
      affiliate: null,
      buy_button_text: 'Comprar',
      is_antifraude: false,
      monthly_interest: 0,
      client_statistics_id: -1
    }
  },
  computed:{
    getCardType () {
      let number = this.data.card_number;
      let re = new RegExp("^4");
      if (number.match(re) != null) return "visa";

      re = new RegExp("^(34|37)");
      if (number.match(re) != null) return "amex";

      re = new RegExp("^5[1-5]");
      if (number.match(re) != null) return "mastercard";

      re = new RegExp("^6011");
      if (number.match(re) != null) return "discover";
      
      re = new RegExp('^9792')
      if (number.match(re) != null) return 'troy'

      return "visa"; // default type
    },
		generateCardNumberMask () {
			return this.getCardType === "amex" ? this.amexCardMask : this.otherCardMask;
    },
    minCardMonth () {
      if (this.cardYear === this.minCardYear) return new Date().getMonth() + 1;
      return 1;
    },
    baseURL(){
      return process.env.VUE_APP_API_HOST;
    }
  },
  watch: {
    data:{
      card_year(){
        if (this.data.card_month < this.minCardMonth) {
          this.data.card_month = "";
        }
      }
    }
  },
  methods: {
    editPaymentDetails(){
      this.completition = 1;
    },
    clickTab(newTabIndex){
      this.submit = false;  
      if(newTabIndex == 0){ 
        this.method_credit_card = true;
      }else{
        this.method_credit_card = false;
      }
    },
    getImgCardBrand(pic) {
      return require('@/assets/img/card/brands/' + pic + '.png')
    },
    getImgUrl(pic) {
      return require('@/assets/img/card/' + pic)
    },
    flipCard (status) {
      this.isCardFlipped = status;
    },
    focusInput (e) {
      this.isInputFocused = true;
      let targetRef = e.target.dataset.ref;
      let target = this.$refs[targetRef];
      this.focusElementStyle = {
        width: `${target.offsetWidth}px`,
        height: `${target.offsetHeight}px`,
        transform: `translateX(${target.offsetLeft}px) translateY(${target.offsetTop}px)`
      }
    },
    blurInput() {
      let vm = this;
      setTimeout(() => {
        if (!vm.isInputFocused) {
          vm.focusElementStyle = null;
        }
      }, 300);
      vm.isInputFocused = false;
    },
    getCEP(){
      
      if(this.data.zipcode.replace("-", "").length >= 8){
        this.loading = true;
       

        axios
        .get('https://viacep.com.br/ws/'+ this.data.zipcode.replace("-", "") +'/json')
        .then(response => {
          
          this.data.street = response.data.logradouro;
          this.data.neighborhood = response.data.bairro;
          this.data.city = response.data.localidade;
          this.data.state = response.data.uf;
          this.loading = false;

        });

      }
    },
    getInstallments(n){

      if(n == 1)
        return this.product.amount;

      var pv = this.product.amount; // product amount
      let i = this.monthly_interest / 100; // tax per month
      let pmt = pv / ( (Math.pow((1 + i), n) - 1) / (Math.pow((1 + i), n) * i) ); 

      return pmt.toFixed(2);
    },
    clientTemp(){
      let data = {
        name: this.data.name,
        email: this.data.email,
        cellphone: this.data.cellphone,
        seller_id: this.product.seller_id,
        product_id: this.product.id
      };

      // Check if there's affiliate with this product
      if(this.affiliate != null){
        data.affiliate_id = this.affiliate.affiliate_id;
      }

      serviceClient
        .create(data)
        .then(response => {
        })
        .catch(err => {
        });

    },
    onSubmit(scope){
      
        this.submit = true;
        this.$validator.validateAll(scope).then((result) => {
          
          if(result){
            if(this.completition == 0){
              var that = this;
              this.loading = true;
              setTimeout(function(){
                that.loading = false;
                that.submit = false;
                that.completition = 1;
              }, 1000);
              this.clientTemp();
              
            }else if(this.completition == 1 && this.method_credit_card){
              this.loading = true;

              let card = {
                card_number: this.data.card_number,
                card_holder_name: this.data.card_holder_name,
                card_expiration_date: this.data.card_expiration_date,
                card_cvv: this.data.card_cvv
              };

              var cardValidations = pagarme.validate({card: card});
              
              // Check if the card number is valid
              if(!cardValidations.card.card_number){
                this.card_number_error = true;
                return;
              }
              
              pagarme.client.connect({ encryption_key: process.env.PAGARME_DEV_CRIPTO })
                .then(client => client.security.encrypt(card))
                .then(card_hash => {

                    // Success, so send the data to the server in order
                    // to make the transaction

                    var cellphone = this.data.cellphone.replace(/[() \-]/g, '');
                    var document_number = this.data.cpf_cnpj.replace(/[.\-/]/g, '');
                    var document_type = document_number.length == 11 ? 'cpf' : 'cnpj';
                    
                    var data = {
                      method: 'CREDIT_CARD',
                      card_hash: card_hash,
                      product_id: this.product.id,
                      installments: this.installments,
                      name: this.data.name,
                      email: this.data.email,
                      ddd: cellphone.substr(0, 2),
                      cellphone: cellphone.substr(2, cellphone.length),
                      cpf_cnpj: this.data.cpf_cnpj,
                      document_type: document_type,
                      document_number: document_number,
                      zipcode: this.data.zipcode.replace(/[-]/g, ''),
                      street: this.data.street,
                      number: this.data.number,
                      complement: this.data.complement,
                      neighborhood: this.data.neighborhood,
                      city: this.data.city,
                      state: this.data.state,
                      client_statistics_id: this.client_statistics_id
                    };

                    // Check if there's affiliate with this product
                    if(this.affiliate != null){
                      data.affiliate_id = this.affiliate.affiliate_id;
                    }

                    servicePayment
                      .create(data)
                      .then(response => {
                        this.loading = false;
                        this.completition = 2;
                        this.charge_success = true;
                        Cookies.remove('affiliate');
                      })
                      .catch(err => {
                        this.loading = false;
                        this.charge_error = true;
                        this.completition = 2;
                      });
                  
                  
                  });



            }else if(this.completition == 1 && !this.method_credit_card){
              this.buildTicket(); // Create the 'boleto'
            }
          }
        });
    },
    buildTicket(){
      this.loading = true;

      var cellphone = this.data.cellphone.replace(/[() \-]/g, '');
      var document_number = this.data.cpf_cnpj.replace(/[.\-/]/g, '');
      var document_type = document_number.length == 11 ? 'cpf' : 'cnpj';

      var data = {
        method: 'BOLETO',
        product_id: this.product.id,
        name: this.data.name,
        email: this.data.email,
        ddd: cellphone.substr(0, 2),
        cellphone: cellphone.substr(2, cellphone.length),
        document_type: document_type,
        document_number: document_number,
        cpf_cnpj: this.data.cpf_cnpj,
        client_statistics_id: this.client_statistics_id
      };

      // Check if there's affiliate with this product
      if(this.affiliate != null){
        data.affiliate_id = this.affiliate.affiliate_id;
      }

       servicePayment
        .create(data)
        .then(response => {
          this.loading = false;
          this.completition = 2;
          this.charge_success = true;
          this.ticket_url = response.boleto_url;
          Cookies.remove('affiliate');
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.charge_error = true;
          this.completition = 2;
        });
    },
    goBack(){

      this.loading = true;
      this.submit = false;
      let that = this;

      setTimeout(function(){
        that.loading = false;
        that.completition = 0;
      }, 1000);
    },
    getProduct(slug, affiliate){

      let data = {
        id: slug, 
        affiliate: affiliate
      };

      serviceProduct
      .read(data)
      .then(response => {
        this.product = response;
        // Load pixels
        this.getMetrics(this.product.seller_id);
        this.getSellerSettings(this.product.seller_id);
        // Save client statistics
        this.setClientStatistics();
      })
      .catch(err => {
        console.log(err);
      });
    },
    getAffiliate(affiliate){

      let data = {
        id: affiliate
      };

      serviceAffiliate
      .read(data)
      .then(response => {
        this.affiliate = response;
      })
      .catch(err => {
        console.log(err);
      });

    },
    getSellerSettings(user_id){

      let data = {
        id: user_id, 
      };

      serviceSettings
      .read(data)
      .then(response => {
        response.forEach(element => {
          if(element.key == 'BUY_BUTTON_TEXT') 
            this.buy_button_text = element.value;
        });
      })
      .catch(err => {
        console.log(err);
      });
    },
    getGlobalSettings(){

      let data = {
        keys: ['PAGARME_ANTIFRAUDE', 'MONTHLY_INTEREST']
      }

      serviceGlobalSettings
      .search(data)
      .then(response => {
        response.forEach(element => {
          if(element.key == 'PAGARME_ANTIFRAUDE'){
            this.is_antifraude = element.value == 'ENABLED' ? true : false;
          }
          if(element.key == 'MONTHLY_INTEREST'){
            this.monthly_interest = parseFloat(element.value);
          }
        });
      })
      .catch(err => {
        console.log(err);
      });
    },
    getMetrics(user_id){

      /*let data = {
        id: user_id
      }

      servicePixel
      .read(data)
      .then(response => {

        response.forEach(element => {
          if(element.type == 'FACEBOOK'){
            fbq('init', element.pixel_id);
            fbq('track', 'PageView');
          }
          if(element.type == 'GOOGLE'){
            ga('create', element.pixel_id, 'auto');
            ga('send', 'pageview');
          }
    

    });

      })
      .catch(err => {
        console.log(err);
      });*/
    },
    setClientStatistics(){

      var parser = new UAParser();
      let result = parser.getResult();

      let data = {
        device_type: result.device_type,
        device_vendor: result.device_vendor,
        engine: result.engine.name,
        browser: result.browser.name,
        os: result.os.name,
        cpu: result.cpu.architecture,
        ua: result.ua,
        seller_id: this.product.seller_id,
        product_id: this.product.id,
        affiliate_id: this.affiliate_id
      };

      serviceClientStatistics
      .create(data)
      .then(response => {
        this.client_statistics_id = response.id;
      })
      .catch(err => {
        console.log(err);
      });
    },

  },
  mounted(){
    var slug = this.$route.params.product;
    var affiliate = Cookies.get('affiliate') || this.$route.params.affiliate;
    this.getProduct(slug, affiliate);
    if(typeof affiliate != 'undefined'){
      this.getAffiliate(affiliate);
    }
    this.getGlobalSettings();

  }

  }
</script>

